import {
    Panel,
    Group,
    Avatar,
    PanelHeader,
    HeaderButton,
    Cell,
    IOS,
    Spinner
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import {Friends} from '../api';

class VueFriendsIsDeleted extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            usersInfo: [],
            isSpinner: true
        };
    }

    componentDidMount () {
        this.getFriends();
    }

    getFriends () {
        let countFriendsInterval = 5000;
        let offsetFriendsInterval = 0;

        this.friendsInterval = setInterval(() => {

            Friends.getFriends({
                count: countFriendsInterval,
                offset: offsetFriendsInterval,
                fields: 'bdate, city, country, photo_200_orig, contacts, online, last_seen',
                access_token: this.props.access_token,
            })
                .then(res => {

                    if(!res.data.response.items.length) {
                        clearInterval(this.friendsInterval);
                    } else {
                        offsetFriendsInterval += countFriendsInterval;

                        res.data.response.items.forEach(item => {
                            if (item.deactivated === 'deactivated' ||
                                item.deactivated === 'banned') {
                                this.setState(state => state.usersInfo.push(item));
                            }
                        });
                        this.setState({isSpinner: false});
                        this.forceUpdate();
                    }
                })
                .catch(e => console.log('ERR GET Friends', e));

        }, 2000);
    }

    prev (e) {
        clearInterval(this.friendsInterval);
        this.props.go(e);
    }

    render () {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.prev.bind(this)} data-to="home">
                        {this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Друзья
                </PanelHeader>
                <Group title={`Друзья в инстаграме ${this.state.usersInfo.length}`}>
                    {
                        this.state.isSpinner &&
                        <Cell size="l">
                            <Spinner size="large" style={{margin: '150px 0'}}/>
                        </Cell>
                    }
                    {
                        this.state.usersInfo &&
                        this.state.usersInfo.map((friend, index) => {
                            return (
                                <div key={index}>
                                    <Cell
                                        size="l"
                                        description={friend.status || ''}
                                        before={<Avatar src={friend.photo_200 || friend.photo_200_orig}/>}
                                        bottomContent={
                                            friend.deactivated === 'deactivated' ? 'Удален' :
                                            friend.deactivated === 'banned' ? 'Забанен': ''
                                        }
                                        onClick={() => window.open(`https://vk.com/id${friend.id}`)}
                                    >
                                        {`${friend.first_name} ${friend.last_name}`}
                                    </Cell>
                                </div>
                            );
                        })
                    }
                </Group>
            </Panel>
        );
    }
}

VueFriendsIsDeleted.propTypes = {
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired
};

export default VueFriendsIsDeleted;
